px) scale(1)}}@-webkit-keyframes dot-1-move{20%{transform:scale(1)}45%{transform:translate(16px, 12px) scale(0.45)}60%{transform:translate(80px, 60px) scale(0.45)}80%{transform:translate(80px, 60px) scale(0.45)}100%{transform:translateY(0px) scale(1)}}@keyframes dot-1-move{20%{transform:scale(1)}45%{transform:translate(16px, 12px) scale(0.45)}60%{transform:translate(80px, 60px) scale(0.45)}80%{transform:translate(80px, 60px) scale(0.45)}100%{transform:translateY(0px) scale(1)}}@-webkit-keyframes rotate-move{55%{transform:translate(-50%, -50%) rotate(0deg)}80%{transform:translate(-50%, -50%) rotate(360deg)}100%{transform:translate(-50%, -50%) rotate(360deg)}}@keyframes rotate-move{55%{transform:translate(-50%, -50%) rotate(0deg)}80%{transform:translate(-50%, -50%) rotate(360deg)}100%{transform:translate(-50%, -50%) rotate(360deg)}}@-webkit-keyframes index{0%,100%{z-index:3}33.3%{z-index:2}66.6%{z-index:1}}@keyframes index{0%,100%{z-index:3}33.3%{z-index:2}66.6%{z-index:1}}

body{background:#000}.header{position:relative;z-index:101}.header .header-wrap{display:flex;align-items:center;justify-content:space-between;height:7rem}.header .header-wrap .header-logo img{width:12rem}.header .header-wrap .header-search{display:flex;align-items:center;width:4.5rem;height:4.5rem;border-radius:3rem;transition:0.3s ease}.header .header-wrap .header-search:hover{background:#fff;width:35rem}.header .header-wrap .header-search:hover input{width:32rem;padding:0 2rem;cursor:pointer}.header .header-wrap .header-search input{background:transparent;border:0;font-size:14px;transition:0.3s ease;width:0;padding:0}.header .header-wrap .header-search input::-webkit-input-placeholder{font-style:italic}.header .header-wrap .header-search input:-ms-input-placeholder{font-style:italic}.header .header-wrap .header-search input::-ms-input-placeholder{font-style:italic}.header .header-wrap .header-search input::placeholder{font-style:italic}.header .header-wrap img{width:2rem;cursor:pointer}

